import React from 'react';
import { Button,
  Header,
  Grid, Container, Image } from 'semantic-ui-react';
import image from '../../../../assets/images/placeholder-large.png'
import localizedConfig from '../../../../i18n';

export default class Home extends React.Component {
  render() {
    return <section id='home'>
             <div className='glass-layer'>
               <Container>
                 <div className='content'>
                   <Header as='h1' className='slogan heading'>
                      {localizedConfig.hello}
                     <span className='part-one'>Az <b>nbizalom</b></span>
                     <span className='part-two'>amit megrdemel</span>
                   </Header>
                   <div className='button-container'>
                     <Button primary>Idpontot krek</Button>
                   </div>
                 </div>
               </Container>
             </div>
           </section>;
  }
}



