import React from 'react';
import { Button,
  Header,
  Grid, Container, Card } from 'semantic-ui-react';
import ProcedureCard from './ProcedureCard.jsx';

export default class Procedures extends React.Component {
  render() {
    const twoLines = <Grid stackable stretched centered columns={ 4 }>
                       <Grid.Column width={ 8 }>
                         <div className='content'>
                           <Header as='h2' className='heading'>Kezelsek</Header>
                           <div className='button-container'>
                             <Button secondary>Teljes rlista</Button>
                           </div>
                         </div>
                       </Grid.Column>
                       <Grid.Column width={ 4 }>
                         <ProcedureCard className='rhino' title='Orrplasztika'></ProcedureCard>
                       </Grid.Column>
                       <Grid.Column width={ 4 }>
                         <ProcedureCard className='breast' title='Mellplasztika'></ProcedureCard>
                       </Grid.Column>
                       <Grid.Column width={ 4 }>
                         <ProcedureCard className='stomach' title='Hasplasztika'></ProcedureCard>
                       </Grid.Column>
                       <Grid.Column width={ 4 }>
                         <ProcedureCard className='ear' title='Flplasztika'></ProcedureCard>
                       </Grid.Column>
                       <Grid.Column width={ 4 }>
                         <ProcedureCard className='wrinkle' title='Rnc feltlts'></ProcedureCard>
                       </Grid.Column>
                       <Grid.Column width={ 4 }>
                         <ProcedureCard className='eyelid' title='Szemhjplasztika'></ProcedureCard>
                       </Grid.Column>
                     </Grid>;
    const oneLine = <Grid stackable stretched centered columns={ 6 }>
                      <Grid.Column>
                        <ProcedureCard className='rhino' title='Orrplasztika'></ProcedureCard>
                      </Grid.Column>
                      <Grid.Column>
                        <ProcedureCard className='breast' title='Mellplasztika'></ProcedureCard>
                      </Grid.Column>
                      <Grid.Column>
                        <ProcedureCard className='stomach' title='Hasplasztika'></ProcedureCard>
                      </Grid.Column>
                      <Grid.Column>
                        <ProcedureCard className='ear' title='Flplasztika'></ProcedureCard>
                      </Grid.Column>
                      <Grid.Column>
                        <ProcedureCard className='wrinkle' title='Rnc feltlts'></ProcedureCard>
                      </Grid.Column>
                      <Grid.Column>
                        <ProcedureCard className='eyelid' title='Szemhjplasztika'></ProcedureCard>
                      </Grid.Column>
                    </Grid>;

    return <section id='procedures'>
             <Container>
               { twoLines }
               <Grid stackable stretched centered className='special'>
                 <Grid.Column>
                   <ProcedureCard className='' title='Hnaplji izzads'></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column>
                   <ProcedureCard className='' title='PRP - Drakula terpia'></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column>
                   <ProcedureCard className='' title='Kopaszsg PRP'></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column>
                   <ProcedureCard className='' title='Brelvltozsok'></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column>
                   <ProcedureCard className='' title='Mezoterpia'></ProcedureCard>
                 </Grid.Column>
               </Grid>
             </Container>
           </section>;
  }
}



