import defaultConfig from './default.js';
import enConfig from './en.js';
import huConfig from './hu.js';

let activeConfig;

switch (process.env.BUILD_LANG) {
  case 'HU': {
    activeConfig = huConfig;
    break;
  }
  case 'EN': {
    activeConfig = enConfig;
    break;
  }
  default: {
    activeConfig = defaultConfig;
    break;
  }
}

console.log('config is: ', activeConfig);

export default activeConfig;



